import React ,{ Component }from "react";
import { FiCast  , FiCpu, FiCreditCard, FiActivity, FiServer, FiSliders } from "react-icons/fi";

const ServiceList = [
    {
        icon: <FiCast />,
        title: 'Mobility',
        description: 'Bring the service to mobile to ease access to the services.'
    },
    {
        icon: <FiCpu />,
        title: 'Distributed Services',
        description: 'Distributed services to connect multiple systems.'
    },
    {
        icon: <FiActivity />,
        title: 'Reporting & Visualization',
        description: 'Ease of access to the business reports and monitoring via visualization.'
    },
    { 
        icon: <FiCreditCard />,
        title: 'Pay as you go',
        description: 'Give chance to the customers to pay as they use only, no offline fees.'
    },
    {
        icon: <FiServer />,
        title: 'Large Scale',
        description: 'Grow as much as in customer bases even touching the billion milestones.'
    },
    { 
        icon: <FiSliders />,
        title: 'CI & CD',
        description: 'Continously develop and integrate on all services and giving power back to the customers.'
    }
]


class ServiceThree extends Component{
    render(){
        const {column } = this.props;
        const ServiceContent = ServiceList.slice(0 , this.props.item);
        
        return(
            <React.Fragment>
                <div className="row">
                    {ServiceContent.map( (val , i) => (
                        <div className={`${column}`} key={i}>
                            <a href="/service-details">
                                <div className="service service__style--2">
                                    <div className="icon">
                                        {val.icon}
                                    </div>
                                    <div className="content">
                                        <h3 className="title">{val.title}</h3>
                                        <p>{val.description}</p>
                                    </div>
                                </div>
                            </a>
                        </div>
                    ))}
                </div>
            </React.Fragment>
        )
    }
}
export default ServiceThree;
